import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SupplierComponent } from './supplier.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { AddSupplierComponent } from './add-supplier/add-supplier.component';
import { DetailSupplierComponent } from './detail-supplier/detail-supplier.component';
import { CreateDataSupplierComponent } from './create-data-supplier/create-data-supplier.component';
import { TranslateModule } from '@ngx-translate/core';
const routes: Routes = [
  {
    path: '',
    component: SupplierComponent
  },
  {
    path: 'add',
    component:  AddSupplierComponent
  },
  {
    path: 'detail',
    component: DetailSupplierComponent
  },
  {
    path: 'create-data',
    component: CreateDataSupplierComponent
  }
]

@NgModule({
  declarations: [
    SupplierComponent,
    AddSupplierComponent,
    DetailSupplierComponent,
    CreateDataSupplierComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'})
  ]
})
export class SupplierModule { }
