import { Component } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhImage, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { CountryCode } from 'libphonenumber-js/max';

@Component({
  selector: 'app-detail-supplier',
  templateUrl: './detail-supplier.component.html',
  styleUrls: ['./detail-supplier.component.scss']
})
export class DetailSupplierComponent {

  editSupplier: FormGroup;
  data: any;
  dataRestore = this.router.getCurrentNavigation().extras.state;
  /** detail được mở từ tab Đã ẩn hay không */
  isFromHidden: boolean;



  constructor(
    private vhComponent: VhComponent,
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public languageService: LanguageService,
    private vhImage: VhImage,
    private functionService: FunctionService,
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
    this.isFromHidden = this.router.getCurrentNavigation().extras.state.isFromHidden;
    this.countryCode = this.data.countryCode ?? this.functionService.defaultCountryCode;
    this.callingCode = this.functionService.getCallingCodeFromCountryCode(this.countryCode) ?? this.functionService.defaultCallingCode;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.data.phone, this.countryCode as CountryCode);

    this.editSupplier = new FormGroup({
      img: new FormControl(this.data.img),
      name: new FormControl(this.data.name, Validators.compose([Validators.required])),
      phone: new FormControl(this.formattedPhone, [Validators.required, this.functionService.phoneValidator(() => this.countryCode)]),
      email: new FormControl(this.data.email, Validators.compose([Validators.email])),
      address: new FormControl(this.data.address),
      province: new FormControl(this.data.province),
      district: new FormControl(this.data.district),
      country: new FormControl(this.data.country),
      note: new FormControl(this.data.note),
      taxcode: new FormControl(this.data.taxcode),
      time_for_payment: new FormControl(this.data.time_for_payment ? this.data.time_for_payment : 0, Validators.compose([Validators.required])),
      delete_hidden: new FormControl(this.data.delete_hidden),
    });
  }



  /** ------ Modal Zorro---- */
  isVisible = false;

  showModal(): void {
    this.isVisible = true;
  }



  handleOk(): void {
    this.deleteSupplier();
  }



  handleCancel(): void {
    this.isVisible = false;
  }



  goBack() {
    this.router.navigate(['/sales/dashboard/manage/partner/supplier'], {
      state: this.dataRestore
    });
  }



  isExitsPhone: boolean = false;
  /**
   * xóa ncc khỏi DB
   * @example this.deleteSupplier()
   */
  deleteSupplier() {
    this.vhQuerySales.deleteSupplier(this.data._id)
      .then(bool => {
        if (bool) {
          if (this.editSupplier.value["img"]) {
            this.vhImage.deleteThumbnail([this.editSupplier.value["img"]]).then(() => {
              setTimeout(() => {
                this.isVisible = false;
                this.vhComponent.showToast(2000, (this.languageService.translate("Supplier has been deleted successfully")), "current-toast");
                this.goBack();
              }, 300);
            })
          } else {
            this.isVisible = false;
            this.vhComponent.showToast(2000, (this.languageService.translate("Supplier has been deleted successfully")), "current-toast");
            this.goBack();
          }
        }
        else {
          this.isVisible = false;
          this.vhComponent.showToast(4000, this.languageService.translate('You cannot delete this supplier because it has generated invoices, but you can hide it and only see it in the reports.'), 'alert-toast');
          // this.functionService.confirmUpdateHidden('customer').then(() => {
          //   this.vhQuerySales.updateSupplier(this.data._id, { delete_hidden: true }).then(() => {
          //     this.vhComponent.showToast(3000, (this.languageService.translate('Successfully hidden!')), 'success-toast');
          //     setTimeout(() => {
          //       this.goBack();
          //     }, 300);
          //   }).catch((err) => { 
          //     console.log(err);
          //   });
          // }).catch(() => { });
        }
      });
  }



  /**
   * cập nhật ncc theo trường truyền vào
   * @example this.updateSupplier({name : 'abc'})
   */
  updateSupplier(value) {
    if (this.editSupplier.valid) {
      let supplier = { ...value };
      if (supplier.phone) {
        supplier.countryCode = this.countryCode;
        supplier.phone = this.functionService.getNationalPhoneNumber(this.editSupplier.value.phone, this.countryCode);
        supplier.phoneNumber = this.functionService.getInternationalPhoneNumber(this.editSupplier.value.phone, this.countryCode);
      }
      console.log('supplier', supplier);
      
      this.vhQuerySales.updateSupplier(this.data._id, supplier)
      .then((rsp:any)=>{
        console.log('rsp', rsp);
        
        if(rsp.vcode == 0) this.isExitsPhone = false;
        else if(rsp.vcode == 1){
         //This phone number is already in use with another customer
          this.isExitsPhone = true;
          this.vhComponent.showToast(1500, this.languageService.translate("This phone number is already in use with another supplier"), 'alert-toast')

        }
        },(error:any)=>{
        console.log('error', error);
        })
    }
  }



  /**
   * mở folder chọn file từ desktop
   * @example this.getFile()
   */
  getFile() {
    document.getElementById("file-upload").click();
  }



  /** Lấy hình ảnh từ Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 256, 256, "suppliers", this.editSupplier.value["img"] || '').then(
      photoURL => {
        this.editSupplier.controls["img"].setValue(photoURL);
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), 'success-toast');
        this.updateSupplier(this.editSupplier.value);
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), 'alert-toast');
      }
    );
  }



  /** Mã quốc gia */
  countryCode: string;
  /** Calling code */
  callingCode: string;
  /** Số điện thoại đã format. VD: 333 333 333 */
  formattedPhone: string;

  /**
   * Mở modal chọn mã quốc gia
   */
  openSearchCountryCode() {
    this.functionService.openSearchCountryCode().then((data) => {
      this.countryCode = data.code;
      this.callingCode = data.callingCode;
      this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.editSupplier.value.phone, this.countryCode as CountryCode);
      this.editSupplier.get('phone')?.markAsTouched();
      this.editSupplier.get('phone')?.markAsDirty();
      this.editSupplier.get('phone')?.updateValueAndValidity();
      this.updateSupplier(this.editSupplier.value);
    }).catch((error) => {
      // console.error('Error opening country code search:', error);
    });
  }



  /**
   * Format số điện thoại khi nhập và set vào form
   * @param event Sự kiện khi nhập số điện thoại
   */
  formatPhoneNumber(event: Event): void {
    // Lấy số điện thoại đã format
    const input = (event.target as HTMLInputElement).value;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(input, this.countryCode as CountryCode);
    // Cập nhật vào form
    this.editSupplier.get('phone')?.setValue(this.formattedPhone, { emitEvent: false });
  }

}
